<template>
    <div>
        <van-nav-bar
        title="找回密码"
        left-text="返回"
        @click-left="toback"
        />
        <van-form @submit="changeOk">
            <van-field
                type="text"
                label="手机号"
                placeholder="注册的手机号"
                left-icon="phone-o"                  
                v-model="phone"
                name="pattern"
                
            >
            </van-field>
            <van-field
                :type="isEye ? 'password':'text'"
                label="密码"
                placeholder="新密码"
                left-icon="lock"
                :right-icon="isEye ? 'closed-eye' : 'eye-o'"
                v-model="password"
                name="pswcl"
                @click-right-icon='isEye=!isEye'
            />
            <div class="register-btn">
                <van-button round block type="info" native-type="submit">提交</van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                phone:'',
                password:'',
                isEye:''
            }
        },
        methods:{
            toback(){
                this.$router.go(-1)
            },
            changeOk(){
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: "/retrievePassword",
                    data: {
                        appkey:this.appkey,
                        phone: this.phone,
                        password: this.password
                    }
                })
                .then(result => {
                    this.$toast.clear();
                    
                    if(result.data.code == 'L001'){
                        this.$router.push({name:'Login'})
                    }
                    
                })
                .catch(err => {
                    this.$toast.clear();
                    
                });
            }
        }
    }
</script>

<style lang="less" scoped>
/deep/.van-button--info{
    background-color: #f4b973;
    border: 1px solid #f4b973;
}
</style>